body {
    font-family: $font-family;
    @include font-properties();
    background-color: var(--white);
}

.wrapper {
    width: 100%;
    max-width: 1310px;
    margin: 0 auto;
    padding: 0 .625rem;
}

a {
    transition: all .2s;

    &:hover {
        transition: all .2s;
    }
}