@mixin font-properties($size: $font-size, $weight: 400, $line-height: 150%, $color: var(--font-color)) {
    font-size: $size;
    font-weight: $weight;
    line-height: $line-height;
    color: $color;
}

// Breakepoints start

@mixin xxl-down {
    @media (max-width: map-get($brekepoints, xxl)) {
        @content;
    }
}

@mixin xxl-up {
    @media (min-width: map-get($brekepoints, xxl)) {
        @content;
    }
}

